import i18n from "i18next";
import { initReactI18next } from "react-i18next";


const resources = {
  en: {
    translation: {
      "home": "Home",
      "news": "News",
      "profile": "Profile",
      "login": 'Log in',
      "logout": 'Log out'
    }
  },
  ua: {
    translation: {
      "home": "",
      "news": "",
      "profile": "",
      "login": '',
      "logout": ''
    }
  }
};

i18n
  .use(initReactI18next) 
  .init({
    resources,
    lng: "en", 
    interpolation: {
      escapeValue: false 
    }
  });

  export default i18n;